.App {
  text-align: center;
  background-color: rgb(5, 0, 21);
}

.main-bg {
  height: 500px;
  background-image: url(./img/mainbg.png);
  background-size: cover;
  background-position: center;
}

.col-card {
  border: 2px solid black;
  border-radius: 10px;
  margin: 7px;
  margin-top: 20px;
  width: auto;
  display: inline-block;
  background-color: white;
}
.col > button {
  border-radius: 20px;
}